<template>
  <div class="header">
    <router-link class="header__link" to="/"><h2>VOCAB-TESTER</h2></router-link>
    <div class="header__link__right">
      <router-link class="header__link" v-bind:class="{linkColor: this.$route.path == '/'}" to="/">HOME</router-link>
      <router-link class="header__link" v-bind:class="{linkColor: this.$route.path == '/test'}" to="/test">TEST</router-link>
      <router-link class="header__link" v-bind:class="{linkColor: this.$route.path == '/words'}" to="/words">WORDS</router-link>
      <!-- <router-link class="header__link" v-bind:class="{linkColor: this.$route.path == '/score'}" to="/score">SCORE</router-link> -->
    </div>
  </div>
  
  <div class="display" v-bind:class="{scrollBar: this.$route.path != '/test'}">
    <router-view v-slot="{ Component }" :word-data="wordData">
      <transition name="display__tra" mode="out-in">
        <component :is="Component"/>
      </transition>
    </router-view>
  </div>
</template>

<script>

    export default {
        data: function() {
            return {
                wordData: {}
            }
        },

        created: async function() {

            let thisVue = this;

            await fetch('https://gist.githubusercontent.com/ShinoharaTa/f967fab6a7fd6e0e289f5ce3d00f2060/raw/f98736b4da6bcbc4cf2ca02a849d4069ea71fa20/1_Basic300.json')
            .then((response) => response.json())
            .then((json) => thisVue.wordData = json);

            let temp_map = thisVue.wordData;
            let idx = 0;

            this.wordData.forEach((value) => {
              if(value.name.indexOf("-") == -1) {
                temp_map[idx] = value;
                idx++;
              }
            });

            thisVue.wordData = temp_map;
        }
    }

</script>


